import { withApollo } from "react-apollo";
import { Component } from "react";

export class ResetStore extends Component {

  constructor(props) {
    super(props);
    this.state = { reset: false };
  }

  unsubscribe = () => ( props.client.onResetStore(
    () => this.setState({ reset: true })
  ));

  render() {
    return (
        <>
            <button
            onClick={() => this.unsubscribe}
            > client.onResetStore 
            </button>

            { this.state.reset ? "Successful" : "Failed"}
        </>
    )
  }
}

export default withApollo(ResetStore);